/**
 * Global state management
 */
export class State {
  constructor() {
    this.state = {
      isInitialized: false,
      timeSpent: 0,
      lastPath: null,
      retryCount: 0
    };
  }

  /**
   * Get state value
   * @param {string} key State key
   * @returns {*} State value
   */
  get(key) {
    return this.state[key];
  }

  /**
   * Set state value
   * @param {string} key State key
   * @param {*} value State value
   */
  set(key, value) {
    this.state[key] = value;
  }

  /**
   * Increment time spent
   * @param {number} seconds Seconds to add
   */
  incrementTimeSpent(seconds) {
    this.state.timeSpent += seconds;
  }

  /**
   * Reset state
   */
  reset() {
    this.state = {
      isInitialized: false,
      timeSpent: 0,
      lastPath: null,
      retryCount: 0
    };
  }

  /**
   * Get all state
   * @returns {Object} All state
   */
  getAll() {
    return { ...this.state };
  }
}